body {
  margin: 0;
  padding: 0;
}
.navbar {
  margin-bottom: 0px;
  padding-left: 3%;
}

.navbar-nav {
  padding-left: 58%;
  word-spacing: normal;
  padding-top: 1%;
  font-size: 13px;
  background-color: white;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
  border-bottom: 3px solid transparent;
}
.navbar > img {
  padding-right: 2%;
  /* align-content: center; */
  height: 75px;
}
@media screen and (max-width: 370px) {
  .intro-bg > img {
    width: 100%;
  }
}
