.feature-Product > h1 {
  font-family: "Cabin", arial, sans-serif;
  font-size: 25px;
  text-align: center;
  /* padding-bottom: 15px; */
  padding-left: 12%;
  font-family: inherit;
  /* border-bottom: 2px solid cyan; */
}
.button-wrapper {
  display: flex;
  flex-direction: row;
  font-size: 30px;
  text-align: center;
}
.feature-Product {
  padding: 10px;
  flex: 90%;
}

.button-wrappers {
  padding: 10px;
  flex: 10%;
}

.border {
  border: white !important;
  padding: 0% 42% 1% 42%;
  background-color: transparent !important;
  border: none;
}

h5.text-right {
  padding-right: 5%;
}

.navbar-nav a:hover {
  border-bottom: 3px solid black;
}

.navbar-nav a.active {
  border-bottom: 3px solid black;
}

